import React, { Component, PropTypes } from 'react';

import ModalContainer from '../../../containers/modal/Modal.container.jsx';

import '../../../assets/boot_scss/bootstrap.scss';

import Timepicker from '../../Timepicker.jsx';
export default class LayoutPage extends Component {
    handleTimepicker(value) {
        console.debug(value);
    }
    render() {
        const { children } = this.props;
        const headerStyle = { textAlign: 'center', paddingTop: '15px' };

        return (
            <div className="container-fluid">
                <div className="row">
                    <div className="col-xs-12 col-sm-12 col-md-12">
                        <h2 style={headerStyle}>Timepicker with clear d3/React</h2>
                    </div>
                </div>
                <hr />
                <Timepicker
                    outerRadius="60"
                    interval="60"
                    boundaryHour="4"
                    showResults
                    getResults={this.handleTimepicker}
                /> 
                <Timepicker
                    outerRadius="100"
                    interval="30"
                    boundaryHour="20"
                    getResults={this.handleTimepicker}
                    showResults
                />
                <Timepicker
                    outerRadius="130"
                    interval="20"
                    boundaryHour="13"
                    getResults={this.handleTimepicker}
                    showResults
                />
                <Timepicker
                    outerRadius="130"
                    interval="15"
                    boundaryHour="13"
                    getResults={this.handleTimepicker}
                    showResults
                /> 
                <Timepicker
                    outerRadius="180"
                    interval="10"
                    boundaryHour="7"
                    getResults={this.handleTimepicker}
                    showResults
                />
            </div> 
        );
    }
}

LayoutPage.propTypes = {
    children: PropTypes.element,
    windowWidth: PropTypes.number,
    handleKeyDown: PropTypes.func,
    containerModalData: PropTypes.object,
    isModalOpen: PropTypes.bool,
};



